import { Col, Row } from "react-bootstrap"
import Container from "react-bootstrap/Container"

const Portfolio_box=()=>{
    let col_Style={
        border:'0',
        padding:'0'
    }
    
    let div_Style={
        margin:'5px',
        border:'black 2px solid',
        overflow:'hidden',
    }

    let img_Style={
        position: 'relative',
        left:'50%',
        transform:'translate(-50%)',
        height:'40vh',
        objectFit:'cover',
        
    }

    return(
        <Container style={{width:'50%'}}>
            <Row >
                <Col sm={8} style={col_Style}>
                    <div style={div_Style}><img src='./portfolio_tb/mdserver.png' style={img_Style}/></div>
                </Col>
                <Col sm={4} style={col_Style}>
                    <div style={div_Style}><img src='./portfolio_tb/mindmap.png' style={img_Style}/></div>
                </Col>
            </Row>
            <Row>
                <Col sm={5} style={col_Style}>
                    <div style={div_Style}><img src='./portfolio_tb/crystall.png' style={img_Style}/></div>
                </Col>
                <Col sm={7} style={col_Style}>
                    <div style={div_Style}><img src='./portfolio_tb/landingpage.png' style={img_Style}/></div>
                </Col>
            </Row>
    </Container>
    )
}

export default Portfolio_box